#AcceuilForm h1.Acceuil{
	font-style: oblique;
	text-align: center;
	margin: 15% auto;
	background: linear-gradient(to right, yellow, pink);
}

body {
	background-color: #C4D7ED;
	font-family: sans-serif;
	min-height: 100vh;
}

.Sign{
	margin: 10px;
	padding: 1.5%;
	background: yellow;
	border: 0;
	border-radius: 10px;
}

#Footer{
	background-color: #ABC8E2;
	margin-top: 210px;
}

/* NavBar */
#NavBar{
	background-color: #ABC8E2;
	border-radius: 30px;
	margin-top: -20px;
	padding-bottom: 0.1px;
	margin-left: -5px;
	margin-right: -5px;
}

#NavBar .Logo{
	position: absolute;
	padding-top: 7px;
}

#NavBar .Logo img{
	border-radius: 40px;	
}

#NavBar .Titre{
	font-style: oblique;
	text-align: center;
	color: yellow;
	text-decoration: none;
}

#NavBar .Titre h1{
	padding-top: 25px; 
}

#NavBar .Titre:hover{
	color: pink;
}

#NavBar .Menu{
	text-align: right;
	margin-top: -30px;
}

#NavBar .Menu ul li{
	list-style-type: none;
}

#NavBar .Menu ul li a{
	text-decoration: none;
	color: yellow;
	margin: 15px 15px;
	padding: 15px;
}

#NavBar .MenuCRS{
	display: none;
}

#NavBar .Menu ul li a:hover{
	color: pink;
}

#NavBar .Menu ul li:hover .MenuCRS{
	display: block;
	margin-bottom: 10px;
	border-style: double;
	border-color: #A1BEDC;
}

#NavBar .Menu ul li:hover .MenuCRS li{
	margin-right: 20px;
	padding: 10px;
}

#NavBar .Menu ul li:hover .MenuCRS li a{
	margin-left: -20px;
}

#NavBar .CRS a:hover{
	color: pink;
}

#NavBar button{
	margin-right: 10px;
	font-size: 15px;
	width: 110px;
	height: 35px;
	background: none;
	border: none;
	color: yellow;
	cursor: pointer;
}

#NavBar button:hover{
	color: pink;
}

/* Login */
.LoginForm .Form_box{
	width: 380px;
	height: 480px;
	position: relative;
	margin: 3% auto;
	background: white;
	padding: 5px;
	overflow: hidden;
	border-radius: 30px;
}

.LoginForm .Button_Box{
	width: 220px;
	margin: 35px auto;
	position: relative;;
	box-shadow: 0 0 20px 9px #E1E6FA;
	border-radius: 30px;
}

.LoginForm .Button{
	padding: 10px 30px;
	cursor: pointer;
	background: transparent;
	border: 0;
	outline: none;
	position: relative;
	margin-left: 70px;
}

.LoginForm #BTN{
	position: absolute;
	width: 220px;
	height: 100%;
	background: linear-gradient(to right, #ABC8E2, #E1E6FA);
	border-radius: 30px;
	transition: 0.5s;
}

.LoginForm div.Checkbox{
	color: #777;
	font-size: 12px;
	margin-left: 30px;
	display: inline-flex;
}

.LoginForm div.Checkbox span{
	position: relative;
	margin-left: -40px;
	padding-right: 100px;
	margin-top: 20px;
}

.LoginForm form button a{
	text-decoration: none;
	color: #375D81;
}

.LoginForm div.Input_login{
	margin-top: -30px;
	margin-left: 12px;
}

.LoginForm .Input-Field{
	margin-top: 40px;
	position: relative;
}

.LoginForm .Submit{
	width: 70%;
	display: block;
	margin: auto;
	background: linear-gradient(to right, #ABC8E2, #E1E6FA);
	border: 0;
	border-radius: 30px;
}

.LoginForm .Subtmit a{
	color : white;
	text-decoration: none;
}

.LoginForm .Submit:hover a{
	color : white;
	transition: 0.3s all;
}

.LoginForm .Checkbox{
	margin: 30px 10px 30px 0;
}

/* Register */
#Create{
	width: 380px;
	height: 700px;
	position: relative;
	margin: 3% auto;
	background: white;
	padding: 5px;
	overflow: hidden;
	border-radius: 30px;
}

#Create h4{
	text-align: center;
	padding-top: 40px;
	margin-bottom: 40px;
	color: #375D81;
}


#Create h5{
	text-align: center;
	color: #375D81;
}

form input{
	width: 80%;
	padding: 10px 20px;
	margin: 5px 5px;
	margin-bottom: 15px;
	border: 0;
	border-bottom: 1px solid #ABC8E2;
}

.btn_box{
	width: 100%;
	margin: 30px auto;
	text-align: center;
}

form button{
	width: 110px;
	height: 35px;
	margin: 0 10px;
	background: linear-gradient(to right, #ABC8E2, #E1E6FA);
	border-radius: 30px;
	border: 0;
	outline: none;
	color: #375D81;
	cursor: pointer;
}

#Create .Gender{
	margin-top: -10px;
	display: flex;
	font-size: 12px;
	justify-content: center;
	color: grey;
}

#Create select{
	margin: 10px 5px;
	border: 0;
	border-bottom: 1px solid #ABC8E2;
	outline: none;
	background: #E1E6FA;
	color: #375D81;
}

#Progression{
	position: absolute;
	height: 100%;
	width: 120px;
	background: linear-gradient(to right, #ABC8E2, #E1E6FA);
	transition: 1s;
}

/* Contact */
#ContactForm .entete h1{
	text-align: center;
	color: #375D81;
}

div#CorpsContact div{
	display: inline-flex;
	width: 49%;
	height: 484px;
}

div#CorpsContact div.Etudiant1{
	width: 360px;
	height: 400px;
	margin: 6% auto;
	margin-left: 17%;
	background: white;
	border-radius: 30px;
	position: relative;
	overflow: hidden;
}

div#CorpsContact div.Etudiant2{
	width: 360px;
	height: 400px;
	margin-left: 15%;
	background: white;
	border-radius: 30px;
	position: relative;
	overflow: hidden;
}

#CorpsContact h4{
	text-align: center;
	margin-bottom: 40px;
	color: #375D81;
}

#CorpsContact .Input{
	left: 10px;
	top: 10px;
	position: relative;
	width: 340px;
	height: 380px;
	transition: 0.5s;
	background-color: #C4D7ED;
	border-radius: 30px;
}

/* Sign */
#Sign button.Button_Sign{
	font-size: 15px;
	width: 110px;
	height: 35px;
	background: none;
	border: none;
	color: yellow;
	cursor: pointer;
}

#Sign button.Button_Sign:hover{
	color: pink;
}